@import url(https://fonts.googleapis.com/css?family=Open+Sans:300);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-size: 16px;
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.app {
  height: 100vh;
  width: 100vw;
}

/* custom scrollbar for messages view */
::-webkit-scrollbar {
  width: 2px;
}

.error {
  position: fixed;
  top: 100px;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  padding: 10px 20px;
  font-size: 16px;
  text-align: center;
  border: 0px;
  border-radius: 4px;
  opacity: 0;
  -webkit-animation: hide-error 1.6s forwards;
          animation: hide-error 1.6s forwards;
}

@-webkit-keyframes hide-error {
  0% {
    top: 100px;
    opacity: 0;
  }

  30% {
    top: 80px;
    opacity: 1;
  }
  60% {
    top: 80px;
    opacity: 1;
  }
  100% {
    top: 60px;
    opacity: 0;
  }
}

@keyframes hide-error {
  0% {
    top: 100px;
    opacity: 0;
  }

  30% {
    top: 80px;
    opacity: 1;
  }
  60% {
    top: 80px;
    opacity: 1;
  }
  100% {
    top: 60px;
    opacity: 0;
  }
}

.user-login {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background: #80b7f5;
}

.login-form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 20rem;
  width: 20rem;
  color: #252525;
  background: #fafafa;
  border-radius: 4px;
  box-shadow: 4px 4px 20px rgba(12, 67, 150, 0.3);
}
.header {
  font-size: 40px;
  margin-bottom: 40px;
}

input {
  font-size: 20px;
  border: none;
  border-radius: 10px;
  padding: 10px;
  margin: 10px;
  text-align: center;
  color: #252525;
  background: #ececec;
}

input:focus,
input:active {
  outline: none;
}

.button {
  background-color: #258bff;
  color: #fff;
  cursor: pointer;
}

.app-header {
  position: fixed;
  top: 0;
  left: 0;
  height: 60px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: #fff;
}

.app-name {
  font-size: 16px;
  margin-left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}
.settings-header {
  background: none;
  border: none;
  margin-right: 20px;
  font-size: 16px;
  font-family: inherit;
  cursor: pointer;
}

.settings-header:focus,
.settings-header:active {
  outline: none;
}

.message-list {
  height: calc(90vh - 60px);
  flex-grow: 1;
}

.message {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 14px;
  margin: 0 14px 0 14px;
  border-bottom: 0.2px solid rgba(0, 0, 0, 0.99);
}

.message-sender {
  font-size: 14px;
  font-weight: 700;
  color: #30373f;
  padding-bottom: 4px;
  margin-right: 10px;
}

.message-text {
  color: #000;
}
.message-form {
  bottom: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.message-input {
  flex: 8 1;
  text-align: left;
  font-size: 16px;
  font-family: inherit;
}

.message-send {
  cursor: pointer;
  flex: 1 1;
}

.main {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
}

.dialog-form {
  background: #ededed;
  padding: 30px;
  border: none;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  text-align: center;
  position: relative;
  font-family: inherit;
}

.cancel {
  font-size: 20px;
  position: absolute;
  top: 20px;
  right: 20px;
  background: none;
  border: none;
  cursor: pointer;
}

.cancel:active,
.cancel:focus {
  outline: none;
}

.settings {
  position: fixed;
  height: 100vh;
  width: 100%;
  background: #fff;
  padding: 24px 120px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.settings-content {
  display: flex;
  flex-direction: column;
}

.settings-themes {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 600px;
  margin-bottom: 20px;
}

.themes {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  width: 200px;
  padding: 20px;
}

.header-secondary {
  font-size: 18px;
}
.theme-box {
  height: 30px;
  width: 30px;
  border: 1px solid #1d1d1d;
  border-radius: 5px;
  cursor: pointer;
}

.blue {
  background: #80b7f5;
}

.red {
  background: #eb4949;
}

.dark {
  background: #181b1f;
}

.logout-btn {
  padding: 6px 10px;
  font-size: 18px;
  background: transparent;
  border: 1px solid black;
  border-radius: 4px;
  cursor: pointer;
  align-self: flex-start;
}

.break {
  background: #181b1f46;
  width: 600px;
  height: 1px;
  margin: 20px 0;
}

.chat-screen {
  position: fixed;
  display: flex;
  flex-direction: row;
  margin-top: 60px;
  height: calc(100vh - 60px);
  width: 100%;
}

.chat {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  justify-content: space-between;
}

.chat-form {
  display: flex;
  flex-direction: column;
}

.typing-indicator {
  height: 10px;
  font-size: 12px;
  font-weight: 700;
  padding: 0 14px 14px 14px;
}

/* side columns */
.column {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  font-size: 18px;
  width: 240px;
}

.list {
  list-style: none;
  height: calc(100vh - 60px - 46px - 80px - 20px);
}

.list-item {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 10px 20px;
  cursor: pointer;
  -webkit-transition: background-color 0.4s ease-in-out;
  transition: background-color 0.4s ease-in-out;
  z-index: 4000;
}

.list-header {
  font-size: 26px;
  padding: 10px 20px;
}

.add-btn {
  height: 60px;
  width: 60px;
  margin: 10px;
  border: none;
  border-radius: 10px;
  font-size: 20px;
  -webkit-transition: border-radius 0.4s;
  transition: border-radius 0.4s;
}

.add-btn:hover {
  cursor: pointer;
  border-radius: 20px;
}

.add-btn:focus,
.add-btn:active {
  outline: none;
}

.presence {
  margin-right: 10px;
  height: 10px;
  width: 10px;
  border: none;
  border-radius: 50%;
}

